import React, { useState, useEffect } from 'react'
import bapr from '../../assets/bapr.png'
import npm from '../../assets/npm.png'
import logee from '../../assets/lgt.png'
import nfcard from '../../assets/nfcard.png'
import {Animated} from "react-animated-css";
import { Link } from 'react-router-dom';
import './styles.scoped.css'
import { Breadcrumb } from 'antd';
import { HomeOutlined, DesktopOutlined } from '@ant-design/icons';

export default function Portfolio() {
    const _windowHeight = window.innerHeight
    const [currentTop, setCurrentTop] = useState(0)
    const [isChangePosition, setIsChangePosition] = useState('left')
    const [imgSrc, setImgSrc] = useState(logee)
    window.onscroll = () => {
        setCurrentTop(document.children.item(0).scrollTop)
    }

    useEffect(() => {
        const result = (currentTop / _windowHeight) + 0.5
        if(currentTop !== 0){
            if(Math.floor(result) % 2 === 0){
                setIsChangePosition('left')
            } else if(Math.floor(result) % 2 === 1) {
                setIsChangePosition('right')
            }
            switch (Math.floor(result)) {
                case 0:
                    setImgSrc(logee)
                    break;
                case 1:
                    setImgSrc(npm)
                    break;
                case 2:
                    setImgSrc(bapr)
                    break;
                case 3:
                    setImgSrc(nfcard)
                    break;
                default:
                    break;
            }
        }
    }, [currentTop, _windowHeight, isChangePosition])
    return (
        <Animated animationIn="fadeInDown" animationOut="fadeOutUp" animationInDuration="1000" animationOutDuration="1000">
            <Breadcrumb>
                <Breadcrumb.Item href="">
                    <Link to="/">
                        <HomeOutlined style={{ color: '#DEBB00', marginRight: '.4rem'}}/>
                        <span style={{color: '#DEBB00'}}>Home</span>
                    </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>
                    <DesktopOutlined style={{ color: '#DEBB00', marginRight: '.2rem'}}/>
                    <span style={{color: '#DEBB00'}}>Portfolio</span>
                </Breadcrumb.Item>
            </Breadcrumb>
            <section className="portfolio">
                <img className={isChangePosition} alt="bapr" src={imgSrc} />
                <Animated animationIn="fadeInDown" animationOut="fadeOutUp" animationInDuration="1000" animationOutDuration="1000">
                    <p className="title">
                        LOGEE 
                        <a rel="noopener noreferrer" href="https://logeetrans.com" target="_blank">
                            https://logeetrans.com
                        </a>
                    </p>
                    <p>
                        LOGEE WEB-ADMIN is a web-based application for Internal
                        Operational admin is useful for
                        manage master data of Logee Cargo Owner and
                        Logee Trucker.
                        <br/>
                        <br/>
                        Technology Stack : React.JS, Redux, Redux-form, Google maps API, 
                        D3 / recharts, react-print, JEST, CSS, JIRA, GITLAB, etc.
                    </p>
                </Animated>
                <Animated animationIn="fadeInDown" animationOut="fadeOutUp" animationInDuration="1000" animationOutDuration="1000">
                    <p className="title">
                        NPM b64-to-file
                        <a rel="noopener noreferrer" href="https://www.npmjs.com/package/b64-to-file" target="_blank">
                            https://www.npmjs.com/package/b64-to-file
                        </a>
                    </p>
                    <p>
                        Simple Package to help converting file data type from Base64 to Object File
                        <br/>
                        <br/>
                        Technology Stack : Javascript, Github, NPM
                    </p>
                </Animated>
                <Animated animationIn="fadeInDown" animationOut="fadeOutUp" animationInDuration="1000" animationOutDuration="1000">
                    <p className="title">
                        BAPR 
                        <a rel="noopener noreferrer" href="http://webapps.bapr.id/" target="_blank">
                            http://webapps.bapr.id/
                        </a>
                    </p>
                    <p>                        
                        It is a web-based e-commerce application for printing photos by simply 
                        uploading your photos, editing and selecting a frame, and finally sending 
                        them to your home using your favorite courier service.
                        <br/>
                        <br/>
                        Technology Stack : React.js, Redux, Redux saga, css framework (ant design, bootstrap), 
                        canvas, image editor sdk, payment gateway, Github, etc.
                    </p>
                </Animated>
                <Animated animationIn="fadeInDown" animationOut="fadeOutUp" animationInDuration="1000" animationOutDuration="1000">
                    <p className="title">
                        NFCARD 
                        <a rel="noopener noreferrer" href="https://www.youtube.com/watch?v=0nQNESG9__s" target="_blank">
                            https://www.youtube.com/watch?v=0nQNESG9__s
                        </a>
                    </p>
                    <p>
                        An online digital name card. Users can input which company they are working for 
                        and generate the name card digitally. The app aso allows you to trade name cards 
                        through NFC (Near Field Communication).
                        <br/>
                        <br/>
                        Back End Technology Stack : Node JS, Express JS, Mongo DB, Mongoose, Redis, 
                        Google Cloud Platform, Mocha
                        <br/>
                        <br/>
                        Front End (WEB) Technology Stack : Javascript, Vue JS, VUEX, NPM, bootstrap
                        <br/>
                        <br/>
                        Front End (Mobile) Technology Stack : Javascript, React-Native, Redux, NFC,
                        React Navigation
                    </p>
                </Animated>
            </section>    
        </Animated>
    )
}
